import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar:navBar,
			imgUrl: app.imgUrl,
			imgRemote:imgRemote,
			date: '',
			dateArr: '',
			bitmap: true,
			record:'',
			list: [],
			loading: true,
			count:0
		};
	},
	methods: {
		urlPage() {
			uni.navigateBack({
				delta: 1
			});
		},
		billdetail(item_id,attr_id) {
			this.count++;
			if(this.count!=1)return;
			setTimeout(()=>{this.count=0},1000)
			var timeArr = rs.thedefaulttime();
			var time = this.dateArr;
			if (time == "") {
				var date = timeArr;
			} else {
				var date = this.dateArr;
			}
			var obj = {
				item_id:item_id,
				attr_id:attr_id,
				date:date
			}
			
			uni.navigateTo({
				url: 'purchase_detail?obj='+JSON.stringify(obj)
			});
		},
		open() {
			this.$refs.calendar.open();
		},
		confirm(e) {
			if (!e.range.before || !e.range.after) {
				rs.Toast("请选择正确的日期区间")
			}else{
				
				let startDate=e.range.data[0];
				let endDate=e.range.data[e.range.data.length-1];
				this.date = startDate + ',' + endDate;
				this.loading=true;
				this.dateArr =  [startDate,endDate];
				this.list = [];
				this.moneyList()
			}
			
		},
		/**
		 * 账单列表
		 */
		moneyList() {
			var that = this;
			var timeArr = rs.thedefaulttime();
			var time = that.dateArr;
			if (time == "") {
				var start = timeArr[0];
				var end = timeArr[1];
			} else {
				var start = that.dateArr[0];
				var end = that.dateArr[1];
			}
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
				start:start,
				end:end,
			}
			that.list = [];
			rs.getRequests("buyRecord", data, (res) => {
				if (res.data.code == 200) {
					that.record = res.data.data;
					if (res.data.data.list != '') {
						that.bitmap = true;
						this.loading = false;
							that.list.push(...res.data.data.list);
					} else {
						this.loading = '空';
						that.bitmap = false;
					}
				} else {
					this.loading = '空';
					rs.Toast(res.data.msg)
				}
			})
		},
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow() {
		var that = this;
		if(app.isReload){
			var timeArr = rs.thedefaulttime();
			that.date = timeArr[0] + ',' + timeArr[1];
			that.moneyList()
		}
		
	},
};
